function calculateWinner(squares, n) {
	for (let i = 0; i < squares.length; i++) {
		const x = i % n; // x là vị trí hàng
		const y = Math.floor(i / n); // y là vị trí cột

		// xét hàng
		if (x + 5 <= n) {
			if (
				squares[i] &&
				squares[i] === squares[i + 1] &&
				squares[i] === squares[i + 2] &&
				squares[i] === squares[i + 3] &&
				squares[i] === squares[i + 4]
			) {
				return { player: squares[i], line: [i, i + 1, i + 2, i + 3, i + 4] };
			}
		}

		// xét cột
		if (y + 5 <= n) {
			if (
				squares[i] &&
				squares[i] === squares[i + n] &&
				squares[i] === squares[i + 2 * n] &&
				squares[i] === squares[i + 3 * n] &&
				squares[i] === squares[i + 4 * n]
			) {
				return { player: squares[i], line: [i, i + n, i + 2 * n, i + 3 * n, i + 4 * n] };
			}
		}

		// Xét đường chéo chính
		if (x + 5 <= n) {
			if (
				squares[i] &&
				squares[i] === squares[i + (n + 1)] &&
				squares[i] === squares[i + 2 * (n + 1)] &&
				squares[i] === squares[i + 3 * (n + 1)] &&
				squares[i] === squares[i + 4 * (n + 1)]
			) {
				return {
					player: squares[i],
					line: [i, i + (n + 1), i + 2 * (n + 1), i + 3 * (n + 1), i + 4 * (n + 1)],
				};
			}
		}

		// Xét chéo phụ
		if (x - 5 >= -1) {
			if (
				squares[i] &&
				squares[i] === squares[i + (n - 1)] &&
				squares[i] === squares[i + 2 * (n - 1)] &&
				squares[i] === squares[i + 3 * (n - 1)] &&
				squares[i] === squares[i + 4 * (n - 1)]
			) {
				return {
					player: squares[i],
					line: [i, i + (n - 1), i + 2 * (n - 1), i + 3 * (n - 1), i + 4 * (n - 1)],
				};
			}
		}
	}

	

	return null;
}

class Game extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			history: [
				{
					squares: Array(36).fill(null),
				},
			],
			stepNumber: 0,
			xIsNext: true,
			isDescending: true,
		};
	}

	handleClick(i) {
		const locations = [
			[1, 1],
			[2, 1],
			[3, 1],
			[1, 2],
			[2, 2],
			[3, 2],
			[1, 3],
			[2, 3],
			[3, 3],
		];
		const history = this.state.history.slice(0, this.state.stepNumber + 1);
		const current = history[history.length - 1];
		const squares = current.squares.slice();
		if (calculateWinner(squares, 6) || squares[i]) {
			return;
		}
		squares[i] = this.state.xIsNext ? 'X' : 'O';
		this.setState({
			history: history.concat([
				{
					squares: squares,
					location: locations[i],
				},
			]),
			stepNumber: history.length,
			xIsNext: !this.state.xIsNext,
		});
	}

	jumpTo(step) {
		this.setState({
			stepNumber: step,
			xIsNext: step % 2 === 0,
		});
	}

	sortHistory() {
		this.setState({
			isDescending: !this.state.isDescending,
		});
	}

	render() {
		const history = this.state.history;
		const current = history[this.state.stepNumber];
		const winner = calculateWinner(current.squares, 6);

		const moves = history.map((step, move) => {
			const desc = move
				? 'Go to move #' + move + ' at location (' + history[move].location + ')'
				: 'Go to game start';
			return (
				<li key={move}>
					<button onClick={() => this.jumpTo(move)}>
						{move === this.state.stepNumber ? <b>{desc}</b> : desc}
					</button>
				</li>
			);
		});

		let status;
		if (winner) {
			status = 'Winner: ' + winner.player + ' at points ' + winner.line;
		} else if (!current.squares.includes(null)) {
			status = 'Draw';
		} else {
			status = 'Next player: ' + (this.state.xIsNext ? 'X' : 'O');
		}

		return (
			<div className="game">
				<div className="game-board">
					<Board
						squares={current.squares}
						onClick={(i) => this.handleClick(i)}
						winningSquares={winner ? winner.line : []}
					/>
				</div>
				<div className="game-info">
					<div>{status}</div>
					<ol>{this.state.isDescending ? moves : moves.reverse()}</ol>
					<button
						onClick={() => {
							this.sortHistory();
						}}
					>
						Sort by: {this.state.isDescending ? 'Descending' : 'Asending'}
					</button>
				</div>
			</div>
		);
	}
}

export default Game;
