import React from 'react';
import Square from '../Square/Square';
import './Board.css';

function Board({ squares, winningSquares, onClick }) {
	// return (
	// 	<div className="board-row">
	// 		{[...Array(9).keys()].map((i) =>
	// 			renderSquare(squares[i], i, winningSquares.includes(i), onClick)
	// 		)}
	// 	</div>
	// );

	// function renderSquare(i) {
	// 	return (
	// 		<Square
	// 			value={squares[i]}
	// 			onClick={() => onClick(i)}
	// 			isWinning={winningSquares.includes(i)}
	// 			key={'square' + i}
	// 		/>
	// 	);
	// }

	const boardSize = 6;
	let createSquares = [];
	for (let i = 0; i < boardSize; ++i) {
		let row = [];
		for (let j = 0; j < boardSize; ++j) {
			// row.push(renderSquare(i * boardSize + j));
			row.push(
				renderSquare(
					squares[i * boardSize + j],
					i * boardSize + j,
					winningSquares.includes(i * boardSize + j),
					onClick
				)
			);
		}
		createSquares.push(
			<div key={i} className="board-row">
				{row}
			</div>
		);
	}

	return <div>{createSquares}</div>;
}

function renderSquare(value, i, isWinSquare, onClick) {
	return (
		<Square
			value={value}
			onClick={() => onClick(i)}
			isWinning={isWinSquare}
			key={'square' + i}
		/>
	);
}

